import { Box, Typography, useTheme } from "@mui/material";
import { Friend } from "Components/Friend";
import WidgetWrapper from "Components/WidgetWrapper";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setFriends } from "features/User/userSlice";

export const FriendListWidget = ({ userId }) => {
  // const dispatch = useDispatch();
  const { palette } = useTheme();
  const token = useSelector((state) => state.user.token);
  const friends_store = useSelector((state) => state.user.user.friends);
  const [friends, setFriends] = useState([]);
  // console.log(friends);

  const getFriends = async () => {
    const response = await fetch(
      `http://localhost:3001/users/${userId}/friends`,
      {
        method: "GET",
        headers: { Authorization: `Bearer ${token}` },
      }
    );
    const data = await response.json();

    setFriends(data.friends);
    // console.log(friends);
    // dispatch(setFriends({ friends: friendsIdList }));
  };

  useEffect(() => {
    getFriends();
  }, [friends_store]); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <WidgetWrapper>
      <Typography
        color={palette.neutral.dark}
        variant="h5"
        fontWeight="500"
        sx={{ mb: "1.5rem" }}
      >
        Friend List
      </Typography>
      <Box display="flex" flexDirection="column" gap="1.5rem">
        {friends ? (
          friends.map((friend) => (
            <Friend
              key={friend._id}
              friendId={friend._id}
              name={`${friend.firstName} ${friend.lastName}`}
              subtitle={friend.occupation}
              userPicturePath={friend.picturePath}
            />
          ))
        ) : (
          <></>
        )}
      </Box>
    </WidgetWrapper>
  );
};
