import { Formik } from 'formik';
import * as yup from 'yup';
import { useDispatch } from 'react-redux';
import { setLogin } from 'features/User/userSlice';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';

import {
  Box,
  Typography,
  useTheme,
  useMediaQuery,
  TextField,
  Button,
  Alert,
  AlertTitle,
} from '@mui/material';

const loginSchema = yup.object().shape({
  email: yup.string().email('invalid email').required('required'),
  password: yup.string().required('required'),
});

const initialValues = {
  email: '',
  password: '',
};

export const LoginForm = ({ setFormType }) => {
  const isNonMobile = useMediaQuery('(min-width:600px)');
  const { palette } = useTheme();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [wrongCred, setWrongCred] = useState(false);

  // console.log(wrongCred);

  const login = async (values, onSubmitProps) => {
    const loggedIn = await fetch('http://127.0.0.1:3001/auth/login', {
      method: 'POST',
      headers: { 'Content-type': 'application/json' },
      body: JSON.stringify(values),
    });

    // console.log(loggedIn.status);

    if (loggedIn.status === 200 || loggedIn.status === 201) {
      const loggedUser = await loggedIn.json();

      onSubmitProps.resetForm();

      if (loggedUser) {
        dispatch(
          setLogin({
            user: loggedUser.user,
            token: loggedUser.token,
          })
        );

        navigate('home');
      }
    } else {
      setWrongCred(true);
    }
  };

  const handleSubmit = async (values, onSubmitProps) => {
    await login(values, onSubmitProps);
    // console.log(values, onSubmitProps);
  };

  return (
    <>
      {wrongCred ? (
        <>
          <Alert severity='warning'>
            <AlertTitle>Wrong Credentials !!</AlertTitle>
            Please provide<strong> correct credentials.</strong>
          </Alert>
          <br></br>
        </>
      ) : (
        <></>
      )}
      <Formik
        onSubmit={handleSubmit}
        validationSchema={loginSchema}
        initialValues={initialValues}
      >
        {({
          values,
          errors,
          touched,
          handleBlur,
          handleChange,
          handleSubmit,
          setFieldValue,
          resetForm,
        }) => (
          <form onSubmit={handleSubmit}>
            <Box
              display='grid'
              gap='30px'
              gridTemplateColumns='repeat(4, minmax(0, 1fr))'
              sx={{
                '& > div': { gridColumn: isNonMobile ? undefined : 'span 4' },
              }}
            >
              <TextField
                label='Email'
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.email}
                name='email'
                error={Boolean(touched.email) && Boolean(errors.email)}
                helperText={touched.email && errors.email}
                sx={{ gridColumn: 'span 4' }}
              />
              <TextField
                label='Password'
                type='password'
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.password}
                name='password'
                error={Boolean(touched.password) && Boolean(errors.password)}
                helperText={touched.password && errors.password}
                sx={{ gridColumn: 'span 4' }}
              />
            </Box>
            <Box>
              <Button
                fullWidth
                type='submit'
                sx={{
                  m: '2rem 0',
                  p: '1rem',
                  backgroundColor: palette.primary.main,
                  color: palette.background.alt,
                  '&:hover': { color: palette.primary.main },
                }}
              >
                LOGIN
              </Button>

              <Typography
                onClick={() => {
                  setFormType('register');
                  resetForm();
                }}
                sx={{
                  textDecoration: 'underline',
                  color: palette.primary.main,
                  '&:hover': {
                    cursor: 'pointer',
                    color: palette.primary.light,
                  },
                }}
              >
                "Don't have an account? Sign Up here."
              </Typography>
            </Box>
          </form>
        )}
      </Formik>
    </>
  );
};
