import { useMediaQuery, Box } from "@mui/material";

import Navbar from "Components/Navbar";
import { useSelector } from "react-redux";
import UserWidget from "Common/widgets/UserWidget";
import CreatePostWidget from "Common/widgets/CreatePostWidget";
import GetPostsWidget from "Common/widgets/GetPostsWidget";
import AdvertWidget from "Common/widgets/AdvertWidget";
import { FriendListWidget } from "Common/widgets/FriendListWidget";

const Home = () => {
  const isNonMobileScreens = useMediaQuery("(min-width:1000px)");
  const { _id, picturePath } = useSelector((state) => state.user.user);
  return (
    <Box>
      <Navbar />
      <Box
        width="100%"
        padding="2rem 6%"
        display={isNonMobileScreens ? "flex" : "block"}
        gap="0.5rem"
        justifyContent="space-between"
      >
        <Box flexBasis={isNonMobileScreens ? "26%" : undefined}>
          <UserWidget userId={_id} picturePath={picturePath} />
        </Box>

        <Box
          flexBasis={isNonMobileScreens ? "42%" : undefined}
          mt={isNonMobileScreens ? undefined : "2rem"}
        >
          <CreatePostWidget picturePath={picturePath} />
          <Box m="2rem 0" />
          <GetPostsWidget userId={_id} />
        </Box>
        {isNonMobileScreens && (
          <Box flexBasis="26%">
            <AdvertWidget />
            <Box m="2rem 0" />
            <FriendListWidget userId={_id} />
          </Box>
        )}
      </Box>
    </Box>
  );
};

export default Home;
