import { Box, useMediaQuery } from "@mui/material";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import Navbar from "Components/Navbar";
import { FriendListWidget } from "Common/widgets/FriendListWidget";
import CreatePostWidget from "Common/widgets/CreatePostWidget";
import { PostWidget } from "Common/widgets/PostWidget";
import UserWidget from "Common/widgets/UserWidget";
import GetPostsWidget from "Common/widgets/GetPostsWidget";

const Profile = () => {
  const [user, setUser] = useState(null);
  const { userId } = useParams();
  const token = useSelector((state) => state.user.token);
  const isNonMobileScreens = useMediaQuery("(min-width:1000px)");

  const getUser = async () => {
    const response = await fetch(`http://localhost:3001/users/${userId}`, {
      method: "GET",
      headers: { Authorization: `Bearer ${token}` },
    });
    const data = await response.json();
    setUser(data);
  };

  useEffect(() => {
    getUser();
  }, []);

  return (
    <Box>
      <Navbar />
      {user ? (
        <Box
          width="100%"
          padding="2rem 6%"
          display={isNonMobileScreens ? "flex" : "block"}
          gap="2rem"
          justifyContent="center"
        >
          <Box flexBasis={isNonMobileScreens ? "26%" : undefined}>
            <UserWidget userId={userId} picturePath={user.user.picturePath} />
            <Box m="2rem 0" />
            <FriendListWidget userId={userId} />
          </Box>
          <Box flexBasis={isNonMobileScreens ? "42%" : undefined}>
            <GetPostsWidget userId={userId} isProfile />
          </Box>
        </Box>
      ) : (
        <></>
      )}
    </Box>
  );
};

export default Profile;
