import { PersonAddOutlined, PersonRemoveOutlined } from '@mui/icons-material';
import { Box, IconButton, Typography, useTheme } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { setFriends } from 'features/User/userSlice';
import FlexBetween from './FlexBetween';
import UserImage from './ProfilePhoto';

export const Friend = ({ friendId, name, subtitle, userPicturePath }) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { _id } = useSelector((state) => state.user.user);
  const token = useSelector((state) => state.user.token);
  const friends = useSelector((state) => state.user.user.friends);
  //   console.log(token);

  // console.log(name, subtitle);

  const { palette } = useTheme();
  const primaryLight = palette.primary.light;
  const primaryDark = palette.primary.dark;
  const main = palette.neutral.main;
  const medium = palette.neutral.medium;

  const isFriend = friends.find((friend) => {
    // console.log(friend, friendId, friend == friendId);
    return friend === friendId;
  });

  const updateFriend = async () => {
    const updated_friends = await (
      await fetch(`http://127.0.0.1:3001/users/updateFriend/${friendId}`, {
        method: 'PATCH',
        headers: { Authorization: `Bearer ${token}` },
      })
    ).json();

    const friendsIdList = updated_friends.friends.map((item) => item._id);

    // console.log(friendsIdList);

    dispatch(setFriends({ friends: friendsIdList }));
  };

  return (
    <FlexBetween>
      <FlexBetween gap='1rem'>
        <UserImage image={userPicturePath} size='55px' />
        <Box
          onClick={() => {
            navigate(`/profile/${friendId}`);
          }}
        >
          <Typography
            color={main}
            variant='h5'
            fontWeight='500'
            sx={{
              '&:hover': {
                color: palette.primary.light,
                cursor: 'pointer',
              },
            }}
          >
            {name}
          </Typography>
          <Typography color={medium} fontSize='0.75rem'>
            {subtitle}
          </Typography>
        </Box>
      </FlexBetween>
      {!(_id === friendId) ? (
        <IconButton
          onClick={updateFriend}
          sx={{ backgroundColor: primaryLight, p: '0.6rem' }}
        >
          {isFriend ? (
            <PersonRemoveOutlined sx={{ color: primaryDark }} />
          ) : (
            <PersonAddOutlined sx={{ color: primaryDark }} />
          )}
        </IconButton>
      ) : (
        <></>
      )}
    </FlexBetween>
  );
};
