import { Box, Typography, Divider, useTheme, Skeleton } from "@mui/material";
import FlexBetween from "Components/FlexBetween";

export const UserSkelton = () => {
  return (
    <>
      <FlexBetween gap="1rem">
        <Skeleton variant="circular" width={60} height={60} animation="wave" />

        <Skeleton
          variant="rectangle"
          width="80%"
          height={70}
          animation="wave"
        />
      </FlexBetween>
      <br />
      <Divider />
      <br />
      <Skeleton variant="text" width="100%" height={30} animation="wave" />
      <Skeleton variant="text" width="100%" height={30} animation="wave" />
      <br />
      <Divider />
      <br />
      <Skeleton variant="text" width="100%" height={30} animation="wave" />
      <Skeleton variant="text" width="100%" height={30} animation="wave" />
      <br />
      <Divider />
      <br />
      <Skeleton variant="text" width="60%" height={30} animation="wave" />

      <Skeleton variant="text" width="100%" height={30} animation="wave" />
      <Skeleton variant="text" width="100%" height={30} animation="wave" />
      <br />
    </>
  );
};
