import { useEffect, useState } from 'react';
import { Box, Divider, IconButton, Typography, useTheme } from '@mui/material';
import { useSelector, useDispatch } from 'react-redux';
import { PostWidget } from './PostWidget';
import { setPosts } from 'features/Post/postsSlice';
import { UserSkelton } from './UserSkelton';

const GetPostsWidget = ({ userId, isProfile = false }) => {
  const posts = useSelector((state) => state.posts);
  const token = useSelector((state) => state.user.token);
  const [allPosts, setAllPosts] = useState([]);

  // console.log(isProfile);

  const dispatch = useDispatch();

  const getAllPosts = async () => {
    const allPosts = await fetch('http://127.0.0.1:3001/posts', {
      method: 'GET',
      headers: { Authorization: `Bearer ${token}` },
    });

    const posts = await allPosts.json();

    // console.log(posts);

    posts.posts = posts.posts.reverse();

    // console.log(posts);

    dispatch(setPosts({ posts }));
  };

  const getUserPosts = async () => {
    const userPosts = await (
      await fetch(`http://127.0.0.1:3001/posts/${userId}`, {
        method: 'GET',
        headers: { Authorization: `Bearer ${token}` },
      })
    ).json();

    // dispatch(setPosts({ posts: userPosts }));
    // console.log(userPosts);
    setAllPosts(userPosts);
  };

  useEffect(() => {
    if (!isProfile) {
      getAllPosts();
    } else {
      console.log('xy');
      getUserPosts();
    }
  }, []);

  const getLatestPosts = async () => {
    let all_Posts;

    if (!isProfile) {
      all_Posts = await fetch('http://127.0.0.1:3001/posts', {
        method: 'GET',
        headers: { Authorization: `Bearer ${token}` },
      });
    } else {
      all_Posts = await fetch(`http://127.0.0.1:3001/posts/${userId}`, {
        method: 'GET',
        headers: { Authorization: `Bearer ${token}` },
      });
    }

    const posts = await all_Posts.json();

    // console.log(posts);

    posts.posts = posts.posts.reverse();

    setAllPosts(posts);
  };

  useEffect(() => {
    // console.log("y");
    getLatestPosts();
    // console.log(allPosts);
  }, [posts]);

  return (
    <>
      {!allPosts.posts ? (
        <UserSkelton />
      ) : allPosts.posts.length > 0 ? (
        allPosts.posts.map(
          ({ _id, user, description, postPhoto, likes, comments }) => (
            <PostWidget
              key={_id}
              postId={_id}
              postUserId={user._id}
              name={`${user.firstName} ${user.lastName}`}
              description={description}
              location={user.location}
              picturePath={postPhoto}
              userPicturePath={user.picturePath}
              likes={likes}
              comments={comments}
            />
          )
        )
      ) : (
        <Box>No posts available</Box>
      )}
    </>
  );
};

export default GetPostsWidget;
